@import url("https://unpkg.com/ionicons@4.5.10-0/dist/css/ionicons.min.css");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.2/font/bootstrap-icons.css");
@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600&display=swap");

@import url("./bootstrap.css");
@import url("./template.css");
@import url("./shimmer.css");

:root {
    --min-main-height: auto;
    --page-header-color: #6236FF;
}



main {
    padding: 56px 0;
    margin-bottom: env(safe-area-inset-bottom);
    margin-top: env(safe-area-inset-top);
    min-height: var(--min-main-height);
}

.appBottomMenu {
    position: fixed !important;
    transition-duration: .4s;
}

.appBottomMenu.hidden {
    bottom: calc(-56px - env(safe-area-inset-bottom));
}

/* ACELERAR ANIUMAO DO FONT AWESOME */
.fa-spin {
    -webkit-animation: fa-spin .5s linear infinite;
    animation: fa-spin .5s linear infinite;
}

/* Bloquear DropDown */
.dropdown-item.disabled,
.dropdown-item.disabled i {

    color: #adb5bd !important;

}

/* Cursos do DropDown */
.dropdown-item {
    cursor: pointer !important;
}

/* Cursor do headerButton  */
.headerButton {
    cursor: pointer !important;
}



/* FullLoader */
#loader.transparent {
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    background: #6236ff00 !important;
}

.image-listview>li:after {
    left: 0px;
}

/* INPUT EMPTY */

.input-group.input-group-big-custom .input-group-text {

    font-size: 28px !important;
    border-bottom: none;

}

.form-control.input-big-custom {
    border: none !important;
    font-size: 28px !important;
}

.form-control.input-big-custom:focus {
    outline: none !important;
    border-bottom-color: transparent !important;
    box-shadow: none !important
}

*:focus {
    outline: none;
}

.dropdown-toggle::after {
    position: absolute;
    right: 15px;
}

/* Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
label,
span,
strong,
ul,
ol,
a,
.pageTitle {
    -webkit-user-select: none !important;
    user-select: none !important;

}




.color-item,
.icon-item {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    min-width: 36px;
    min-height: 36px;
    width: 3.3rem;
    height: 3.3rem;
    border-radius: 100%;
    margin: 3px;
    cursor: pointer;
}

.icon-item {
    font-size: 18px;
    background-color: #C5C5C7;
    color: #000;
}

.icon-item.selected {
    background-color: #B07126;
    color: #fff;
}


.color-item.selected::after {
    content: "\f00c";
    font-family: "Font Awesome 5 Pro";
    font-size: 18px;
    color: white;
    display: block;
    position: relative;
}



.expense-component .icon-box {
    position: absolute;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    border-radius: 100%;
    margin-right: 20px;
}

.expense-component .expense-infos {
    margin-left: 70px;
}

.expense-component [name="title"] {
    display: block;
    color: #27173E;
    margin-bottom: -5px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    /* max-width: 130px; */
}

body.dark-mode .expense-component [name="title"] {
    color: white;
}

.expense-component [name="valuePending"] {
    display: block;
    color: #FF396F;
    margin-bottom: -5px;
}

.expense-component [name="value"] {
    font-size: 11px !important;
    color: #958d9e;
}


.category-item .icon-box {
    font-size: 18px !important;
}

.category-item .icon-box i {
    margin: 0 !important;
    padding: 0 !important;
}

.dropdown-select .dropdown-menu {
    overflow-x: hidden;
    overflow-y: scroll;
}

.dropdown-select .dropdown-menu.show {
    position: fixed !important;
    left: 50% !important;
    top: 50% !important;
    width: 80% !important;
    margin: 0px;
    transform: translate(-50%, -50%) !important;
    z-index: 9999;
}

