.category-item.category-item-sub .icon-box {
    min-height: 30px !important;
    min-width: 30px !important;
    width: 30px !important;
    height: 30px !important;
}

.category-item.category-item-sub::before {
    content: "";
    position: absolute;
    left: 30px;
    top: 10%;
    height: 40%;
    width: 15px;
    border-left: 2px dashed gray;
    border-bottom: 2px dashed gray;
}

.form-check-big label::before {
    width: 26px !important;
    height: 26px !important;
    left: 1px !important;
    top: 4px !important;
}

.form-check-big label::after {
    width: 30px !important;
    height: 30px !important;

}

.form-check-big .form-check-input:checked~.form-check-label::after {
    background-color: #1dcc70 !important;
    border-color: #2cd37a !important;
}